import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';
import { BehaviorSubject } from 'rxjs';
import { ResponseToken } from '../external-api/responseToken';
import { Usuario } from '../external-api/usuario';
import { CensoService } from './censo.service';
import { EncoderService } from './encoder.service';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private loginStatus$ = new BehaviorSubject<boolean>(false);
  loginStatusObservable = this.loginStatus$.asObservable();

  constructor(
    private router: Router,
    private censoService: CensoService,
    private encoderService: EncoderService,
    private cookieService: CookieService
  ) { }

  checkToken(): boolean {
    return !this.checkExpireDateToken(this.encoderService.decrypt(this.cookieService.get('token')!))
  }

  checkExpireDateToken(token: string) {
    const expiry = (JSON.parse(atob(token.split('.')[1]))).exp;
    return (Math.floor((new Date).getTime() / 1000)) >= expiry;
  }

  private isLocalDomain(): boolean {
    return window.location.hostname === 'localhost' || window.location.hostname === '127.0.0.1';
  }
  
  saveToken(token: string): void {
    const isLocal = this.isLocalDomain();
    const hostName = isLocal ? undefined : '.hogueras.es';
    const options = {
      domain: hostName,
      path: '/',
      secure: !isLocal // Assuming secure cookies should not be set for localhost
    }
    this.cookieService.set('token', this.encoderService.encrypt(token), options);
  }

  async login(user: string, password: string) {
    let usuario: Usuario = {
      user,
      password: this.encoderService.encrypt(password)
    }
    return new Promise(async (resolve, reject) => {
      this.censoService.doLogin(usuario).subscribe({
        next: (res: ResponseToken) => {
          console.log(res);
          this.saveToken(res.token!);
          this.loginStatus$.next(true);
          resolve(res)
        },
        error: error => {
          console.log(error);
          this.loginStatus$.next(false);
          reject(error)
        }
      });
    });

  }

  public getToken() {
    let token = '';
    if (this.cookieService.get('token')) {
      token = this.encoderService.decrypt(this.cookieService.get('token')!);
      this.loginStatus$.next(true);
    }
    return token;
  }

  logout() {
    this.cookieService.delete('token');
    this.router.navigateByUrl('login');
    this.loginStatus$.next(false);
  }

  isLoggedIn(): boolean {
    const token = this.cookieService.get('token');
    const isLoggedIn = token !== null && token !== '' ? this.checkToken() : false;
    this.loginStatus$.next(isLoggedIn);
    return isLoggedIn;
  }
}
