import { Component } from '@angular/core';
import { Subscription } from 'rxjs';
import { AlertType } from './alertType.enum';
import { FfsjAlertService } from './ffsj-alert.service';

@Component({
  selector: 'lib-ffsj-alert',
  standalone: true,
  imports: [],
  templateUrl: './ffsj-alert.component.html',
  styleUrl: './ffsj-alert.component.scss'
})
export class FfsjAlertComponent {
  public message!: string | null;
  public type!: AlertType;
  public subscription!: Subscription;

  constructor(private ffsjAlertService: FfsjAlertService) { }

  ngOnInit() {
    this.subscription = this.ffsjAlertService.alert$.subscribe(alert => {
      this.message = alert.message;
      this.type = alert.type;
      setTimeout(() => this.closeAlert(), alert.duration);
    });
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  closeAlert() {
    this.message = null;
  }
}
