import { HttpClientModule } from '@angular/common/http';
import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormControl, ReactiveFormsModule } from '@angular/forms';
import { AuthService } from './services/auth.service';

@Component({
  selector: 'lib-ffsj-login',
  standalone: true,
  imports: [
    ReactiveFormsModule,
    HttpClientModule
  ],
  templateUrl: './ffsj-login.component.html',
  styleUrl: './ffsj-login.component.scss'
})
export class FfsjLoginComponent {

  @Input() title: string = 'Iniciar Sesi√≥n';
  @Input() subtitle: string = 'Acceso a administraci√≥n';

  @Output() logStatus: EventEmitter<boolean> = new EventEmitter<boolean>();

  username = new FormControl('');
  password = new FormControl('');
  loading: boolean = false;
  
  constructor(
    private authService: AuthService,
  //   private alertService: AlertService
  ) {}

  // ngOnInit() {
  //   if (this.authService.isLoggedIn()) {
  //     this.route.navigateByUrl('home');
  //   }
  // }

  async login() {
    console.log('Doing Login -> ', this.username.value, this.password.value);
    
    this.loading = true;
    if(this.username.valid && this.password.valid) {
      this.authService.login(this.username.value!, this.password.value!)
        .then((res) => {
          this.loading = false;
          console.log(res);
          
          console.log('Login success');
          this.logStatus.emit(true);
        })
        .catch(() => {
          this.loading = false;
          console.log('Login failed');
          this.logStatus.emit(false);
        });
    }
  }
  
}
